:root {
  --bright-orange: hsl(31, 77%, 52%);
  --dark-cyan: hsl(184, 100%, 22%);
  --very-dark-cyan: hsl(179, 100%, 13%);
  --transparent-white-p: hsla(0, 0%, 100%, 0.75);
  --very-light-gray-bg-h-btn: hsl(0, 0%, 95%);
}

:root {
  --lexend-deca: "Lexend Deca", sans-serif;
  --big-shoulders-display: "Big Shoulders Display", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 0.9375rem;
  font-family: var(--lexend-deca);
  background: var(--very-light-gray-bg-h-btn);
}

figure {
  margin: 0;
}

h1,
h2,
h3 {
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

mark {
  background: none;
}

button {
  border: none;
  background: none;
}

body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0.625rem;
  overflow: hidden;
  margin: 5.5rem 0;
}

.subcontainer {
  width: min-content;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
@media (min-width: 64em) {
  .subcontainer {
    width: auto;
  }
}

.card {
  width: 20.4375rem;
  padding: 2.9375rem 3.0625rem;
}
@media (min-width: 64em) {
  .card {
    padding-right: 1.875rem;
  }
}
.card-sedans {
  background: var(--bright-orange);
}
.card-suvs {
  background: var(--dark-cyan);
}
.card-luxury {
  background: var(--very-dark-cyan);
}
.card__figure {
  width: 4rem;
  height: 2.5rem;
  margin-bottom: 2.5625rem;
}
.card__figure-sedans {
  background: url(../images/icon-sedans.svg);
}
.card__figure-suvs {
  background: url(../images/icon-suvs.svg);
}
.card__figure-luxury {
  background: url(../images/icon-luxury.svg);
}
.card__title {
  font-family: var(--big-shoulders-display);
  font-weight: 700;
  font-size: 2.5rem;
  text-transform: uppercase;
  color: var(--very-light-gray-bg-h-btn);
  margin-bottom: 2.4375rem;
}
.card__description {
  font-weight: 400;
  color: var(--transparent-white-p);
  line-height: 1.5625rem;
  margin-bottom: 1.75rem;
}
@media (min-width: 64em) {
  .card__description {
    letter-spacing: 1px;
    margin-bottom: 5.375rem;
  }
}
.card__btn {
  width: 9.125rem;
  height: 3rem;
  border-radius: 1.5rem;
  background: var(--very-light-gray-bg-h-btn);
  font-weight: 700;
  letter-spacing: 0.0625rem;
  text-transform: capitalize;
}
@media (min-width: 64em) {
  .card__btn {
    font-size: 1rem;
    letter-spacing: 0;
  }
}
.card__btn-sedans {
  color: var(--bright-orange);
}
.card__btn-suvs {
  color: var(--dark-cyan);
}
.card__btn-luxury {
  color: var(--very-dark-cyan);
}